
.header{
    box-shadow: 0px 3px 1px -2px rgba(0,0,0,.2), 0px 2px 2px 0px rgba(0,0,0,.14), 0px 1px 5px 0px rgba(0,0,0,.12);
    padding: 15px;
    background-color: black;
    color: white;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
}
.background{
  height: 700px;
  width: 900px;
}
.shoppies-data{
    margin-top: 80px;
    
}

.internal-header{
    padding-top: 10px;
    font-size: 30px;
    font-weight: 900;
    color: #8d99ae;

}
.logo{
    width: 200px;
    height: 100px;
    
}

/* Movie Card Css */

img {
    -webkit-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    user-drag: none;
  }
  img {
  pointer-events: none;
  }
  .movie_card{
  padding: 0 !important;
  width: 15rem;
  margin:14px 10px; 
  border-radius: 10px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.2), 0 4px 15px 0 rgba(0, 0, 0, 0.19);
  }
  .movie_card img{
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 15rem;
  }
  .movie-info-section{
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
  }
  .movie-info-section button{
      font-size: 12px;
  }

  @media screen and (max-width:375){
    .movie_card{width: 45px;}

  }
  .movie-info-section button:focus, .movie-info-section button:active{
    box-shadow: none !important;
}
  .movie_info{
  color: #5e5c5c;
  }
  
  .movie_info i{
  font-size: 20px;
  }
  .card-title{
  height: 4rem;
  font-size: 18px;
  }
  
  .credits{
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
  border: 2px solid #8e24aa;
  font-size: 18px;
  }
  .credits .card-body{
  padding: 0;
  }
  .credits p{
  padding-top: 15px;
  padding-left: 18px;
  }
  .credits .card-body i{
  color: #8e24aa;
  }
  .movies-list{
      display: flex;
  }

  .search-movies{
    width: 70%;
  }
  .search-movies-list{
    display: flex;
    flex-wrap: wrap;
  }
  .nomination-movies{
      width: 30%;
     
  }
  .nomination-movies .movie_card{
      width: 96%;
      margin-left: 0;
  }
  .nomination-movies .card-title{
      height: auto;
  }
  /* Movie card css ends */
  .max-alert{
    box-shadow: 0px 3px 1px -2px rgba(0,0,0,.2), 0px 2px 2px 0px rgba(0,0,0,.14), 0px 1px 5px 0px rgba(0,0,0,.12);
    width: 96%;
    height: 60px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #FF3D00;
    color: #FF3D00;
    font-weight: bold;
  }

  .search-input {
    position: absolute;
    top: 10px;
    left: 38px;
    font-size: 14px;
    background: none;
    color: #5a6674;
    width: 195px;
    height: 20px;
    border: none;
    appearance: none;
    outline: none;
  
  }

  .search-box{
    position: relative;
    width: 350px;
    height: 40px;
    border-radius: 40px;
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
    background: #fff;
    transition: all 0.3s ease;
  }
  
  .search-button {
    position: absolute;
    top: 10px;
    left: 15px;
    height: 20px;
    width: 20px;
    padding: 0;
    margin: 0;
    border: none;
    background: none;
    outline: none!important;
    cursor: pointer;
  }
 .search-box svg{
    width: 20px;
    height: 20px;
    fill: #5a6674;
 }
 .verticalLine {
    border-left: thick solid #272121;
    height: 1100;
    margin: 20px 15px 20px 0px;
  }


.progress {
	width: 96%;
}

.search-icon{
    height: 100%;
    width: 40px;
    display: flex;
    align-items: center;
    padding-left: 10px;
}
.search-icon img{
    height: 16px;
}